import { create } from 'zustand';
import { devtools } from 'zustand/middleware';
const isEmptyStore = store => Object.keys(store).length === 0;
export const useSchemaStore = create()(devtools(set => ({
  schemas: {},
  setSchemas: schemas => set({
    schemas
  })
})));
export const useSchemas = () => {
  const schemas = useSchemaStore(state => state.schemas);
  if (isEmptyStore(schemas)) {
    // schemas should be loaded before rest of the app (and this is used)
    throw new Error('Schemas not loaded');
  }
  return schemas;
};